export default {
	data() {
		return {
			accountForm: {
				username: '',
				account: '',
				password: '',
				theserver: '',
				owner: '',
				remark: '',
				isOnline: 0
			},
			accountFormArr:[
				['username', '游戏昵称'],
				['account', '账号'],
				['password', '密码'],
				['theserver', '服务器'],
				['owner', '号主'],
				['remark', '备注']
			]
		}
	},
	methods: {
		formSubmit () {
			var data = this.accountForm
			let loginRules = [
			  {name: 'account',type: 'required', errmsg: '账号为必填项'},
			  {name: 'password', type: 'required', errmsg: '密码为必填项'},
			]
			let valLoginRes = this.$validate.validate(data, loginRules)
			if (!valLoginRes.isOk) {
			  uni.showToast({
			    icon: 'none',
			    title: valLoginRes.errmsg
			  })
			  return false
			}
			uni.request({
			    url: '/v1/addAccount',
			    data: data,
				method: 'POST',
			    success: (res) => {
			        if(res.data == "添加成功"){
			        	uni.showToast({
			        	  icon: 'none',
			        	  title: res.data
			        	})
			        	setTimeout(() => {
			        		uni.switchTab({
			        		    url: '/pages/account/index'
			        		})
			        	},1000)
			        }
			    }
			})
		},
		backList () {
			uni.navigateBack({
			    delta: 1
			});
		},
		changeOnlie (e) {
			if (this.accountForm.isOnline==0){
				this.accountForm.isOnline = 1
			}else{
				this.accountForm.isOnline = 0
			}
		}
	}
}
